
import React, { useEffect, forwardRef } from 'react';
import PropTypes from "prop-types"
import { makeStyles } from '@material-ui/core/styles';
import clsx from "clsx"

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import Link from '@material-ui/core/Link';
import Button from '@material-ui/core/Button';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import Slide from '@material-ui/core/Slide';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
    fontWeight: 700,
  },
  transparentBackground: {
    backgroundColor: "rgb(12 24 48 / 0.7)",
  },
  offset: theme.mixins.toolbar,
}));

const HeaderNavBar = React.forwardRef(({ siteTitle, position }, ref) => {
  const classes = useStyles();
  const preventDefault = (event) => event.preventDefault();

  return (
    <AppBar
      position={position}
      ref={ref}
      elevation={position === "fixed" ? 4 : 0}
      className={clsx({
        [classes.transparentBackground]: position === "fixed",
      })}
    >
      <Container maxWidth="lg">
        <Toolbar>
          <Typography variant="h6" className={classes.title}>
            <Link color="textPrimary" href="/" onClick={preventDefault} underline="none">
              {siteTitle}
            </Link>
          </Typography>
          <Button color="inherit">Login</Button>
        </Toolbar>
      </Container>
    </AppBar>
  );
});

const Header = ({ siteTitle }) => {
  const classes = useStyles();
  const trigger = useScrollTrigger({ threshold: 64 });

  return (
    <div className={classes.root}>

      {!trigger &&
        <HeaderNavBar siteTitle={siteTitle} position={"static"} />
      }
      <Slide appear={false} direction="down" in={trigger}>
        <HeaderNavBar siteTitle={siteTitle} position={"fixed"} />
      </Slide>
      {trigger &&
        <div className={classes.offset} />
      }
    </div >
  );
}

HeaderNavBar.propTypes = {
  siteTitle: PropTypes.string,
  position: PropTypes.string,
  ref: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.any })
  ])
}

HeaderNavBar.defaultProps = {
  siteTitle: ``,
  position: `static`,
  ref: undefined,
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
